<!-- 车辆监控 -->
<template>
	<el-container style="position:relative;">
		<div class="side-p">
			<div @contextmenu.prevent class="side-box" :class="asideOpend ? 'aside-trans-top' : 'aside-trans-buttom'">
				<div class="side-num">
					<div class="current">车辆列表</div>
					<el-tooltip class="item" effect="dark" content="在线" placement="bottom">
						<el-button size="small" style="color:#a9d11c;margin:0;padding: 9px 5px;text-algin:center;min-width:50px;border-color:#a9d11c;background:#eefadc;float:left;margin: 0 0 0 12px;font-size:14px;" class="icon iconfont">&#xe62c;{{ wholeNum }}</el-button>
					</el-tooltip>
					<el-tooltip class="item" effect="dark" content="离线" placement="bottom">
						<el-button  size="small" style="color:#bfbfbf;margin:0;padding: 9px 5px;text-algin:center;min-width:50px;border-color:#bfbfbf;background:#eee;float:left;margin: 0 0 0 12px;font-size:14px;" class="icon iconfont">&#xe62c;{{ onLineNum }}</el-button>
					</el-tooltip>
					<el-tooltip class="item" effect="dark" content="告警" placement="bottom">
						<el-button size="small" style="color:#e67060;padding: 9px 5px;margin:0;text-algin:center;min-width:50px;border-color:#e67060;background:#f9ddd5;float:left;margin: 0 0 0 12px;font-size:14px;" class="icon iconfont">&#xe62c;{{ offLineNum }}</el-button>
					</el-tooltip>
					<span v-if="!asideOpend" @click="clickTransHeightDown" class="el-icon-arrow-down"></span>
					<span v-if="asideOpend" @click="clickTransHeightUp" class="el-icon-arrow-up"></span>
				</div>
				<el-input class="side-search" placeholder="请输入车牌号" v-model="searchInput">
					<i slot="suffix" class="el-input__icon el-icon-search"></i>
				</el-input>
				<!-- 树 -->
				<el-aside width="100%"  style="margin: 10px 0 0;flex:1;">
					<div id="down-side" class="border-blue" @mousedown.prevent="downSide" style="position:relative;">
						<el-tree empty-text default-expand-all :expand-on-click-node="expandNode" @node-expand="nodeExpand" @node-collapse="nodeCollapse" @check="changeChecked" :data="sideTree" show-checkbox node-key="id" @node-click="handleNodeSideClick" highlight-current :default-expanded-keys="openList" ref="sideTree" :props="defaultProps">
							<span class="custom-tree-node" slot-scope="{ node, data }">
								<span v-if="data.type=='children' && data.onLine" style="color:#a9d11c;font-size:14px;display:inline-block;" class="icon iconfont rotate-icon">&#xe61b;</span>
								<span v-if="data.type=='children' && !data.onLine" style="color:#bfbfbf;font-size:14px;" class="icon iconfont">&#xe601;</span>
								<span v-if="data.type=='node' && data.opend" style="color:#00a8d4;font-size:12px;" class="icon iconfont">&#xe602;</span>
								<span v-if="data.type=='node' && !data.opend" style="color:#00a8d4;font-size:14px;" class="icon iconfont">&#xe642;</span>
								<span>{{ node.label }}</span>
								<span style="color:#e67060;font-size:14px;" class="icon iconfont">&#xe60a;</span>
							</span>
						</el-tree>
						<ul @mousedown.stop v-if="treePopupShow" class="tree-popup" :style="{top:clientY,left:clientX}">
							<li @click.stop="clickPopupLi('0')">设备信息</li>
							<li @click.stop="clickPopupLi('1')">历史轨迹</li>
							<li @click.stop="clickPopupLi('2')">统计报表</li>
							<li @click.stop="clickPopupLi('3')">命令下发</li>
							<li @click.stop="clickPopupLi('4')">地理围栏</li>
						</ul>
					</div>
				</el-aside>	
			</div>
		</div>
		<!-- 右边地图 -->
		<el-aside style="width:100%;position:relative;display: flex;flex-direction: column;overflow:hidden;">
			<div id="map"></div>
			<!-- 天气和工具 -->
			<div class="tool-bar clearFloat">
				<div class="left">
					<a href="javascript:;" class="left-a" @click="downIsUpIconClick('city')">
						<img src="../../assets/image/carMonitor/toolIndex1.png"/>
						{{ curCity }}
						<span v-if="!cityDownIsUpShow" class="el-icon-arrow-down"></span>
						<span v-if="cityDownIsUpShow" class="el-icon-arrow-up"></span>
					</a>
					<!-- 当前城市 -->
					<div v-if="curCityShow" class="cur-city">
						<ul class="hot-ul">
							<li @click="clickHotCity(v.city)" v-for="(v,index) in hotCityList">{{ v.city }}</li>
						</ul>
						<div class="city-type">
							<a href="javascript:;">按省份</a>
							<a href="javascript:;">按城市</a>
							<el-row class="row" style="float:right;">
								<el-input size="mini" placeholder="输入城市名" v-model="citySearch">
									<i slot="suffix" style="cursor:pointer;height:26px;" class="el-input__icon el-icon-search"></i>
								</el-input>
							</el-row>
						</div>
						<ul class="letter-list">
							<li v-for="(v,index) in letterProvinceList">{{ v }}</li>
						</ul>
						<!-- 按照省份的首字母分类 -->
						<el-aside width="100%" style="height:200px;">
							<ul class="prov-letter-type">
								<li v-for="(v,index) in letterProveDataList">
									<span class="letter">{{ v.name }}</span>
									<ul class="prov-type">
										<li v-for="(prov,key) in v.children">
											<span @click="clickProv(prov.name)" class="prov">{{ prov.name }}</span>
											<ul class="city-type">
												<li @click="clickCity(city.name)" v-for="(city,i) in prov.children">{{ city.name }}</li>
											</ul>
										</li>
									</ul>
								</li>
							</ul>
						</el-aside>
					</div>	
				</div>
				<span class="border"></span>
				<div class="right">
					<a class="right-a" href="javascript:;" @click="downIsUpIconClick('tool')">
						<img src="../../assets/image/carMonitor/toolIndex2.png"/>
						工具
						<span v-if="!toolDownIsUpShow" class="el-icon-arrow-down"></span>
						<span v-if="toolDownIsUpShow" class="el-icon-arrow-up"></span>
					</a>
				</div>
			</div>
			<!-- 车辆详情弹窗 -->
			<div v-if="carPopupHide" class="car-info">
				<div class="title">
					{{ carInfo.carNum }}
					<span @click="clickPopupHide" class="hide el-icon-close"></span>
				</div>
				<div class="content">
					<div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;">
						<span style="float: left;color: #444;display: block;">速度：</span>
						<em style="float: left;color: #999;display: block;">{{ carInfo.spped }}</em>
					</div>
					<div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;">
						<span style="float: left;color: #444;display: block;">本月行驶里程：</span>
						<em style="float: left;color: #999;display: block;">{{ carInfo.mileage }}</em>
					</div>
					<div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;">
						<span style="float: left;color: #444;display: block;">定位时间：</span>
						<em style="float: left;color: #999;display: block;">{{ carInfo.locationDate }}</em>
					</div>
					<div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;">
						<span style="float: left;color: #444;display: block;">定位地址：</span>
						<em style="float: left;color: #999;display: block;">{{ carInfo.locationAddress }}</em>
					</div>
					<div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;">
						<span style="float: left;color: #444;display: block;">司机姓名：</span>
						<em style="float: left;color: #999;display: block;">{{ carInfo.driverName }}</em>
					</div>
					<div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;">
						<span style="float: left;color: #444;display: block;">轮胎情况：</span>
						<em class="text color-red">{{ carInfo.tyreInfo }}</em>
						<a href="javascript:;" style="float: right;color: #4dc4f5;display: block;text-decoration: underline;">点击查看详情</a>
					</div>
					<div class="btn-bar">
						<router-link to="/home/carMonitor/allMonitor">实时监控</router-link>
					</div>
				</div>
			</div>
			<!-- 报警信息 -->
			<!-- <div class="transitons-box" :class="downIsUpShow ? 'trans-top' : 'trans-buttom'">
				<p class="msg-title">
					报警信息
					<span v-if="downIsUpShow" @click="transitionDown" class="el-icon-caret-bottom"></span>
					<span v-if="!downIsUpShow" @click="transitionUp" class="el-icon-caret-top"></span>
				</p>
				<el-table class="center" size="mini" max-height="140" :data="tableData3" style="width: 100%;text-align:center;" height="250">
					<el-table-column type="index" label="序号" width="100"></el-table-column>
					<el-table-column prop="date" label="日期" width="150"></el-table-column>
					<el-table-column prop="name" label="姓名" width="120"></el-table-column>
					<el-table-column prop="province" label="省份" width="120"></el-table-column>
					<el-table-column prop="city" label="市区" width="120"></el-table-column>
					<el-table-column prop="address" label="地址" width="300"></el-table-column>
					<el-table-column prop="zip" label="邮编" width="120"></el-table-column>
				</el-table>
			</div> -->
		</el-aside>
	</el-container>

</template>

<script>
var map;
var City = ()=>import('../../conmon/js/address.js')
var PinYin = ()=>import('../../conmon/js/pinyin.js')
export default {
	name: 'allMonitor',
	data() {
		return {
			tableData3: [{
				date: '2016-05-03',
				name: '王小虎',
				province: '上海',
				city: '普陀区',
				address: '上海市普陀区金沙江路 1518 弄',
				zip: 200333
			}, {
				date: '2016-05-02',
				name: '王小虎',
				province: '上海',
				city: '普陀区',
				address: '上海市普陀区金沙江路 1518 弄',
				zip: 200333
			}, {
				date: '2016-05-04',
				name: '王小虎',
				province: '上海',
				city: '普陀区',
				address: '上海市普陀区金沙江路 1518 弄',
				zip: 200333
			}, {
				date: '2016-05-01',
				name: '王小虎',
				province: '上海',
				city: '普陀区',
				address: '上海市普陀区金沙江路 1518 弄',
				zip: 200333
			}, {
				date: '2016-05-08',
				name: '王小虎',
				province: '上海',
				city: '普陀区',
				address: '上海市普陀区金沙江路 1518 弄',
				zip: 200333
			}, {
				date: '2016-05-06',
				name: '王小虎',
				province: '上海',
				city: '普陀区',
				address: '上海市普陀区金沙江路 1518 弄',
				zip: 200333
			}, {
				date: '2016-05-07',
				name: '王小虎',
				province: '上海',
				city: '普陀区',
				address: '上海市普陀区金沙江路 1518 弄',
				zip: 200333
			}],
			wholeNum: '5000',
			onLineNum: '2500',
			offLineNum: '20',
			searchInput: '',
			expandNode: false,
			cityDownIsUpShow: false,
			toolDownIsUpShow: false,
			curCity: '呼和浩特',
			carPopupHide: false,
			selectDate: '',
			celectDateIcon: false,
			footTabCurIndex: null,
			carTypeCurIndex: '0',
			downIsUpShow: false,
			tableShow: false,
			clientX: '0',
			clientY: '0',
			treePopupShow: false,
			asideOpend: true,
			asideShow: false,
			curCityShow: false,
			carInfo: {
				carNum: '粤B23456',
				spped: '5km/s',
				mileage: '8999km',
				locationDate: '2018-07-11 17:00:00',
				locationAddress: '广东省深圳市南山区',
				driverName: '老张',
				tyreInfo: '轮胎异常，胎压异常'
			},
			sideTree: [{
				id:'1',
				text: '深圳赤子物流有限公司',
				type: 'node',
				opend: true,
				children: [{
					id:'2',
					type: 'node',
					text: '操作部',
					opend: true,
					children: [{
						id:'7',
						type: 'children',
						text: '粤C24523',
						onLine: false
					},
					{
						id:'9',
						type: 'children',
						text: '粤C24873',
						onLine: true
					}]
				},
				{
					id:'3',
					text: '新车',
					type: 'node',
					opend: true,
					children: [{
						id:'10',
						type: 'children',
						text: '粤C24523',
						onLine: false
					},
					{
						id:'11',
						type: 'children',
						text: '粤C24873',
						onLine: true
					}]
				}]
			}],
			openList: [],
			defaultProps: {
				children: 'children',
				label: 'text'
			},
			hotCityList: [{
				city: '全国'
			},
			{
				city: '北京'
			},
			{
				city: '天津'
			},
			{
				city: '上海'
			},
			{
				city: '重庆'
			},
			{
				city: '深圳'
			},
			{
				city: '广州'
			},
			{
				city: '西安'
			},
			{
				city:'香港'
			}],
			letterCityList:['A','B','C','D','E','F','G','H','J','K','L','M','N','P','Q','R','S','T','W','X','Y','Z'],
			letterProvinceList:['A','F','G','H','J','L','N','Q','S','X','Y','Z' ,''],
			letterProveDataList: [],
			citySearch: '',
			visible2: true
		}
	},
	created() {
		var that = this;
		var cityList = [],pinyin;
		City().then(value=>{
			cityList = value.city;
			return PinYin();
		})
		.then(value=>{
			pinyin = value.default.pinyin;
			var porveList = that.getProveList(cityList);
			//将that.letterProveDataList循环成一个树的数组，一级是字母，二级是省，三级是市
			for(var i in that.letterProvinceList) {
				that.letterProveDataList.push({name:that.letterProvinceList[i]});
				for(var j in porveList) {
					//判断首字母与字母数组匹配
					if(pinyin.getCamelChars(porveList[j]).substr(0,1) == that.letterProvinceList[i]){
						if(that.letterProveDataList[i].children != undefined && that.letterProveDataList[i].children.length > 0) {
							that.letterProveDataList[i].children.push({children: cityList[j].sub,name: cityList[j].name});
						} else {
							that.letterProveDataList[i].children = [];
							that.letterProveDataList[i].children.push({children: cityList[j].sub,name: cityList[j].name});
						}
					}

				}
			}
		})
	},
	mounted() {
		// this.qeuryLocation();
		map = new BMap.Map('map', {enableMapClick:false});
		var point = new BMap.Point(108.840053, 34.129522);
		map.centerAndZoom(point, 10);
		map.enableScrollWheelZoom();
		map.addControl(new BMap.ScaleControl());
		var bs = map.getBounds();   //获取可视区域
		var bssw = bs.getSouthWest();   //可视区域左下角
		var bsne = bs.getNorthEast();   //可视区域右上角	
    	var topLat = bsne.lat;
    	var bottomLat = bssw.lat;
    	var leftLng = bssw.lng;
    	var rightLng = bsne.lng;
    	map.addEventListener('click',()=>{
    		console.log('点击了地图！')
    	})
    	map.onclick = ()=>{
    		console.log('onclick点击了地图！')
    	}
    	map.addEventListener('dragging',()=>{
    		console.log('拖拽地图！')
    	})
    	map.addEventListener('moving',()=>{
    		console.log('移动地图！')
    	})
    	map.addEventListener('resize',()=>{
    		console.log('地图可视区发生变化！')
    	})
    	map.addEventListener('zoomend',()=>{
    		console.log('地图缩放级别结束！')
    	})
    	console.log()
		// map.addControl(new BMap.NavigationControl());
	},
	methods: {
		getProveList(list) {
			var porveList = [];
			for(var i in list) {
				porveList.push(list[i].name);
			}
			return porveList;
		},
		loadBMapScript () {
			let script = document.createElement('script');
			script.id = 'script';
			script.src = 'http://api.map.baidu.com/api?v=2.0&ak=dF8sMa2zZUk8znVfxyvzxQjsiQWrDfW2&callback=bMapInit';
			document.body.appendChild(script);
		},
		downIsUpIconClick(data){
			if(data == 'city') {
				this.cityDownIsUpShow = !this.cityDownIsUpShow;
			} else {
				this.toolDownIsUpShow = !this.toolDownIsUpShow;
			}
		},
		qeuryLocation() {
			window['init'] = ()=>{

			}
			map = new BMap.Map('map', {enableMapClick:false});
			var point = new BMap.Point(108.840053, 34.129522);
			map.centerAndZoom(point, 15);
			map.enableScrollWheelZoom();
			map.addControl(new BMap.ScaleControl());
			map.addControl(new BMap.NavigationControl());
			var pointStr = "104.047789,30.648128,104.063959,30.655336,104.067264,30.660307,104.067264,30.664438,104.064008,30.665316,104.074008,30.665316,104.084008,30.665316,104.094008,30.665316,104.104008,30.665316,104.114008,30.665316,104.124008,30.66531,104.134008,30.67531,104.144008,30.68531,104.154008,30.69531,104.164008,30.70531,104.164008,30.71531".split(",");

			var pointArr = [];

			for (var k = 0; k < pointStr.length; k += 2) {
				pointArr.push({
					lng: pointStr[k],
					lat: pointStr[k + 1]
				});
			}
			// 生成坐标点
			var trackPoint = [];
			var lineColor = '#00b7ee';
			for(var i = 0;i < pointArr.length-1;i++) {
				trackPoint = [];
				trackPoint.push(new BMap.Point(pointArr[i].lng, pointArr[i].lat));
				trackPoint.push(new BMap.Point(pointArr[i+1].lng, pointArr[i+1].lat));
       		    //区间颜色
       		    if(i == 1){
       		    	lineColor="yellow";
       		    }else if(i == 2){
       		    	lineColor="green";
       		    }else if(i == 3){
       		    	lineColor="black";
       		    }else if(i == 4) {
       		    	lineColor="red";
       		    }
				// 画线
				var polyline = new BMap.Polyline(trackPoint, {
					strokeColor: lineColor,
					strokeWeight: 3,
					strokeStyle:"dashed",
					strokeOpacity: 1
				});
				map.addOverlay(polyline);
			}
       		// 配置图片
       		var size = new BMap.Size(18, 18);
       		var offset = new BMap.Size(0, 0);
       		var imageSize = new BMap.Size(18, 18);
       		var icon = new BMap.Icon(require("../../assets/image/map/mapTrailIcon1.png"), size, {
       			imageSize: imageSize,
       			infoWindowAnchor:new BMap.Size(0, -3)
       		});
       		// 画图标
       		for (var i = 0;i < pointArr.length;i++) {
       			var track = new BMap.Point(pointArr[i].lng, pointArr[i].lat)
       			var marker = new BMap.Marker(track, {
       				icon: icon,
       				offset: offset
       			});
       			// 创建标注
       			map.addOverlay(marker);
       			marker.addEventListener("click",function () {

       				var point=this.getPosition();
       			})
       		}
       		this.setZoom(pointArr,map);
       	},
       	//根据经纬极值计算绽放级别。 (从网上复制)
       	getZoom(maxLng, minLng, maxLat, minLat,map) {
	        var zoom = ["50", "100", "200", "500", "1000", "2000", "5000", "10000", "20000", "25000", "50000", "100000", "200000", "500000", "1000000", "2000000"]; // 级别18到3。
	        var pointA = new BMap.Point(maxLng, maxLat); // 创建点坐标A
	        var pointB = new BMap.Point(minLng, minLat); // 创建点坐标B
	        var distance = map.getDistance(pointA, pointB).toFixed(1); //获取两点距离,保留小数点后两位
	        for (var i = 0, zoomLen = zoom.length; i < zoomLen; i++) {
	        	if (zoom[i] - distance > 0) {
	                return 18 - i + 3; //之所以会多3，是因为地图范围常常是比例尺距离的10倍以上。所以级别会增加3。
	            }
	        }
	    },
	    //设置地图缩放
	    setZoom(points,map) {
	    	if (points.length > 0) {
	    		var maxLng = points[0].lng;
	    		var minLng = points[0].lng;
	    		var maxLat = points[0].lat;
	    		var minLat = points[0].lat;
	    		var res;
	    		for (var i = points.length - 1; i >= 0; i--) {
	    			res = points[i];
	    			if (res.lng > maxLng) maxLng = res.lng;
	    			if (res.lng < minLng) minLng = res.lng;
	    			if (res.lat > maxLat) maxLat = res.lat;
	    			if (res.lat < minLat) minLat = res.lat;
	    		}
	    		var cenLng = (parseFloat(maxLng) + parseFloat(minLng)) / 2;
	    		var cenLat = (parseFloat(maxLat) + parseFloat(minLat)) / 2;
	    		var zoom = this.getZoom(maxLng, minLng, maxLat, minLat,map);
	    		map.centerAndZoom(new BMap.Point(cenLng, cenLat), zoom ? zoom : 5);
	    	} else {
	            //没有坐标，显示全中国
	            map.centerAndZoom(new BMap.Point(103.388611, 35.563611), 12);
	        }
	    },
	    changeChecked(data) {
	    	var checkedList = this.$refs.sideTree.getCurrentKey();
	    	var checkedNode = this.$refs.sideTree.getCheckedNodes();
	    	var checkedkey = this.$refs.sideTree.getHalfCheckedKeys();
	    	var that = this;
	    	// this.$refs.sideTree.setCurrentNode({id:'11',text: '粤C24873'});
	    	// this.$refs.sideTree.setCurrentKey('1');
	    	var myIcon = new BMap.Icon(require('../../assets/image/map/hc.png'), new BMap.Size(18,34));
	    	var pt = new BMap.Point(108+Math.random(), 34.1+Math.random());
       		var marker = new BMap.Marker(pt,{icon:myIcon});  // 创建标注
			map.addOverlay(marker);              // 将标注添加到地图中
			marker.setRotation(parseInt(Math.random()*(360-5+1)+5));//旋转角度
			var zoom = map.getZoom();
			map.centerAndZoom(pt,zoom);
			var label = new BMap.Label(data.text, {
				offset: new BMap.Size(-30, -30)
			});
			label.setStyle({
				width: "70px",
				color: '#333',
				background: '#dfdfdf',
				border: '1px solid "#ff8355"',
				borderRadius: "0",
				textAlign: "center",
				height: "14px",
				padding: '5px 0'
			});
			marker.setLabel(label); 
			var steelContent = '<div style="width: 340px;padding: 0 0 10px;z-index: 18;" class="car-info"><div style="font-size: 16px;color: #333;height: 40px;line-height: 40px;position: relative;padding: 0 20px;" class="title">'+this.carInfo.carNum+'</div><div style="background: #fff;padding: 0 20px 10px;" class="content"><div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;"><span style="float: left;color: #444;display: block;">速度：</span><em style="float: left;color: #999;display: block;">'+this.carInfo.spped+'</em></div><div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;"><span style="float: left;color: #444;display: block;">本月行驶里程：</span><em style="float: left;color: #999;display: block;">'+this.carInfo.mileage+'</em></div><div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;"><span style="float: left;color: #444;display: block;">定位时间：</span><em style="float: left;color: #999;display: block;">'+this.carInfo.locationDate+'</em></div><div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;"><span style="float: left;color: #444;display: block;">定位地址：</span><em style="float: left;color: #999;display: block;">'+this.carInfo.locationAddress+'</em></div><div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;"><span style="float: left;color: #444;display: block;">司机姓名：</span><em style="float: left;color: #999;display: block;">'+this.carInfo.driverName+'</em></div><div class="li-bar" style="overflow:hidden;line-height:30px;font-size:14px;"><span style="float: left;color: #444;display: block;">轮胎情况：</span><em class="text color-red">'+this.carInfo.tyreInfo+'</em><a href="javascript:;" id="more" style="float: right;color: #4dc4f5;display: block;text-decoration: underline;">点击查看详情</a></div><div class="btn-bar" style="overflow: hidden;padding: 10px 0 0;"><a href="javascript:;" style="display: block;padding: 0 10px;height: 26px;line-height: 26px;background: #0ba5dc;column-rule-color: #fff;float: left;margin-right: 10px;" >实时监控</a></div></div></div>';
			var steelOpts = {
				width : 380,
				height: 270,
				title : "" ,
				enableMessage:true,
				offset: new BMap.Size(0, -20)
			};
			new BMap.InfoWindow(steelContent, steelOpts);
			marker.addEventListener("click",()=>{
				marker.openInfoWindow(new BMap.InfoWindow(steelContent, steelOpts));
				var Omore = document.getElementById('more');
				var Aa = document.querySelector('.btn-bar').children;
				for(var i = 0;i < Aa.length;i++) {
					(function(i){
						Aa[i].onclick = function() {
							switch(i)
							{
								case 0:
								that.$router.push('/home/carMonitor/carAnalysis');
								default:
							}
						}
					}(i))
				};
				Omore.onclick = function() {
					
				}
			})
		},
		//往树节点的内容插入图标
		renderContent(h, { node, data, store }) {
			
		},
		//节点展开
		nodeExpand(data,node,cur) {
			data.opend = !data.opend;
		},
		//节点闭合
		nodeCollapse(data,node,cur) {
			data.opend = !data.opend;
		},
		handleNodeSideClick() {
		},
		clickPopupShow() {
			this.carPopupHide = true;
		},
		clickPopupHide() {
			this.carPopupHide = false;
		},
		clickFootTab(index) {
			this.footTabCurIndex = index;
		},
		clickCarType(index) {
			this.carTypeCurIndex = index;
		},
		//动画
		transitionDown() {
			this.downIsUpShow = false;
		},
		transitionUp() {
			this.downIsUpShow = true;
		},
		downSide(e) {
			if(e.button == '0') {
				this.treePopupShow = false;
			} else {
				this.treePopupShow = true;
				this.clientX = e.pageX + 'px';
				this.clientY = (e.pageY - 240 - e.offsetY) + 'px';
			}
			
		},
		clickPopupLi(index) {
			switch(index) {
				case '0':

				break;
				case '1':
				this.$router.push('/home/carMonitor/carAnalysis');
				break;
				case '2':
				
				break;
				case '3':
				
				break;
				case '4':
				
				break;
				default:
			}
		},
		//点击热门城市
		clickHotCity(city) {
			if(city == '全国') {
				map.centerAndZoom(city,5);
			} else {
				map.centerAndZoom(city,10);
			}
		},
		clickProv(prov) {
			map.centerAndZoom(prov,9);
		},
		clickCity(city) {
			map.centerAndZoom(city,11);
		},
		//aside的展开动画
		clickTransHeightDown() {
			// if(!this.asideShow){return;}
			this.asideOpend = true;
			setTimeout(()=>{
				this.asideShow = true;
			},500)
		},
		//aside的折叠动画
		clickTransHeightUp() {
			// if(this.asideShow){return;}
			this.asideOpend = false;
			setTimeout(()=>{
				this.asideShow = false;
			},500)
		}
	}
}
</script>

<style scoped>
#map{width: 100%;padding: 0;flex: 1;}
.side-num{text-align: center;line-height: 36px;font-size: 12px;height: 36px;}
.side-num span{display: block;color: #666;line-height: 36px;float: right;cursor: pointer;}
.side-num .current{color: #333;float: left;}
.side-search{margin: 10px 0 0;}
.side-search .search-input{color: #666;box-sizing: border-box;}
.side-search .el-icon-search{cursor: pointer;}
.side-p{padding: 10px 0 40px 10px;width:330px;position: absolute;top: 0;left: 0;height: 100%;box-sizing: border-box;z-index: 999;}
.side-box{padding: 10px 10px;display:flex;flex-direction:column;background: #fff;height: 100%;box-sizing: border-box;overflow: hidden;}
.btn{width: 200px;height: 48px;line-height: 48px;text-align: center;background: #303c4d;color: #fff;font-size: 14px;margin: 30px auto 0;display: block;border: none;border-radius: 4px;}

.tool-bar{position: absolute;top: 10px;right: 10px;z-index: 19;background: #303c4d;border-radius: 4px;padding: 5px;min-width: 188px;}
.tool-bar a{float: left;height: 21px;line-height: 21px;font-size: 12px;color: #fff;display: block;}
.tool-bar a img{float: left;width: 26px;height: 21px;margin-right: 5px;}
.tool-bar .border{display: block;width: 1px;height: 21px;background: #fff;margin: 0 10px;float: left;}
.tab-btn{overflow: hidden;border: 1px solid #616f82;}
.tab-btn li{float: left;cursor: pointer;border-right: 1px solid #616f82;color: #666;font-size: 14px;width: 98px;height: 44px;line-height: 44px;text-align: center;box-sizing: border-box;}
.tab-btn .active{color: #fff;background: #303c4d;border: none;}
.right-popup{position: absolute;top: 20px;right: 20px;z-index: 19;overflow: hidden;width: 330px;}
.right-popup .lengthKm-bar{overflow: hidden;}
.right-popup .length-km,.right-popup .length-time{float: left;width: 160px;border-radius: 4px;color: #fff;text-align: center;padding: 5px 0 0;}
.right-popup .length-km{background: #eb6877;}
.right-popup .length-time{background: #18baf4;float: right;}
.right-popup .length-km .name,.right-popup .length-time .name{display: block;line-height: 26px;font-size: 14px;padding: 32px 0 0;background: url(../../assets/image/map/lengthKmIndex1.png) top center no-repeat;}
.right-popup .length-time .name{background: url(../../assets/image/map/lengthKmIndex2.png) top center no-repeat;}
.right-popup .length-km .num,.right-popup .length-time .num{font-size: 18px;line-height: 30px;}
.right-popup .more-bar{width: 100%;border: 1px solid #666;border-radius: 4px;padding: 20px 10px 0;box-sizing: border-box;background: #fff;margin: 20px 0 0;position: relative;}
.right-popup .radio{overflow: hidden;}
.right-popup .radio span{display: block;float: left;line-height: 20px;margin-right: 10px;color: #666;}
.right-popup .radio a{display: block;float: right;line-height: 20px;margin-right: 10px;color: #18baf4;margin-right: 30px;text-decoration: underline;}
.right-popup .type{padding: 15px 0 0;display: flex;}
.right-popup .type li{flex: 1;border-right: 1px solid #666;padding: 20px 0;display: flex;justify-content: center;flex-direction: column;align-items: center;}
.right-popup .type li:last-child{border: none;}
.right-popup li .num{display: block;color: #999;line-height: 30px;font-size: 22px;padding-bottom: 16px;text-align: center;}
.right-popup li .color-red{color: #fc0101;}
.more-bar .hide{position: absolute;display: block;font-size: 20px;top: 3px;right: 5px;color: #c5c5c5;line-height: 24px;}
.msg-title{padding: 0 16px;color: #333;font-size:14px;border-bottom: 1px solid #0ba5dc;height: 44px;line-height: 44px;}
.msg-title span{float: right;font-size: 20px;cursor: pointer;margin: 11px 0 0;color: #333;}
.example {transform: translate3d(0, 0, 0);}
.fold-enter-active, .fold-leave-active {transition: all 1s;}
.fold-enter, .fold-leave-active {transform: translate3d(0, 140px, 0);}
.trans-buttom{transition: all .5s;height: 44px;}
.trans-top{height: 184px;transition: all .5s;}
.aside-trans-buttom{transition: all .5s;height: 56px !important;}
.aside-trans-top{transition: all .5s;height: 100% !important;}
.transitons-box{overflow: hidden;}
.tree-popup{position: absolute;width: 100px;padding: 5px 0;background: #fff;color: #5f6477;font-size: 13px;box-shadow: 0 0 10px rgba(0,0,0,.3);left: 10px;top: 0;z-index: 999;}
.tree-popup li{line-height: 25px;text-align: center;cursor: pointer;}
.cur-city{position: absolute;top: 60px;right: 0;z-index: 20;background: #fff;width: 370px;padding: 10px;color: #0ba5dc;font-size: 12px;}
.hot-ul{overflow: hidden;}
.hot-ul li{float: left;margin-left: 14px;line-height: 24px;cursor: pointer;}
.city-type{overflow: hidden;background: #f5f5f5;padding: 5px;}
.city-type a{float: left;display: block;color: #333;height: 28px;line-height:28px;padding: 0 5px;margin-top: 6px;}
.letter-list{overflow: hidden;padding: 14px 10px;border-bottom: 1px solid #dcdcdc;}
.letter-list li{margin-right: 15px;float: left;line-height: 14px;}
.prov-letter-type{overflow: hidden;}
.prov-letter-type li{overflow: hidden;}
.prov-letter-type .letter{display: block;float: left;font-size: 20px;font-weight: bold;color: #333;}
.prov-letter-type .prov-type{float: right;overflow: hidden;width: 320px;}
.prov-letter-type .prov-type li{overflow: hidden;}
.prov-letter-type .prov-type li .prov{font-size: 12px;float: left;display: block;font-weight: bold;cursor: pointer;color: #666;}
.prov-letter-type .prov-type .city-type{float: right;overflow: hidden;width: 270px;margin-right: -10px;background: #fff;padding-top: 8px;}
.prov-letter-type .prov-type .city-type li{float: left;margin-right: 10px;line-height: 24px;cursor: pointer;color: #666;}
.rotate-icon{-webkit-animation: icon-rotate 1s linear infinite;-moz-animation: icon-rotate 1s linear infinite;-ms-animation: icon-rotate 1s linear infinite;-o-animation: icon-rotate 1s linear infinite;animation: icon-rotate 1s linear infinite;}	
@-webkit-keyframes icon-rotate{
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
@-moz-keyframes icon-rotate{
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
@-ms-keyframes icon-rotate{
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
@-o-keyframes icon-rotate{
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
@keyframes icon-rotate{
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
</style>



